article {
    font-family: 'Raleway';
    height: 600px; 
    width: 800px; 
    display: flex;
    flex-direction: column;
}


h1 {
   font-size: 35px;
   letter-spacing: 1.5px;
   margin-bottom: 2rem;
}

p {
   font-size: 25px;
}

@media screen and (max-width: 420px) {
   article {
    height: 400px;
   }

h1 {
    font-size: 25px; 
}

p {
    font-size: 22px;
}

  }; 
